/* General Styles */
.product-page-cont {
  display: flex;
  justify-content: center;
  width: 100%;
  background: #f8eaea;
  padding-bottom: 28px;
}
.product-page {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  max-width: 1200px;
  gap: 50px;
}

.product-image-gallery {
  flex: 1;
  min-width: 250px;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-image-container {
  position: relative;
  width: 100%;
  max-width: 450px;
}

.main-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.thumbnail-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  gap: 10px;
}

.thumbnail {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: 2px solid transparent;
  border-radius: 5px;
}

.thumbnail.active {
  border: 2px solid #c08497; /* Highlight color for the active thumbnail */
}

.prev-arrow,
.next-arrow {
  position: absolute;
  top: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  transform: translateY(-50%);
}

.prev-arrow {
  left: 10px;
}

.next-arrow {
  right: 10px;
}
/*right*/
.right {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}
.right-container {
  display: flex;
  flex-direction: column;
}
.pro-name,
.pro-qty-buy {
  border-bottom: 1px solid rgb(0, 0, 0);
}

.pro-name h2 {
  margin-bottom: 20px;
}

.pro-qty-buy h6 {
  font-size: 36px;
  color: #5d5857;
  padding: 20px 0;
}

.pro-qty-buy p {
  display: inline-block;
}
.pro-qty-buy select {
  padding: 5px;
  border-radius: 7px;
  margin-left: 5px;
  border: 2px solid #bc8f8f;
}

.pro-btn {
  display: flex;
  justify-content: space-between;
  margin: 20px 0 30px 0;
  width: 100%;
}
.pro-btn :nth-child(2) {
  margin-right: 0;
}

.buy-btn-cont,
.add-cart-btn-cont {
  flex: 1;
  width: 100%;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  border: none;
  margin-right: 20px;
  border-radius: 10px;
}
.buy-btn:hover,
.add-cart-btn:hover {
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15);
}
.buy-btn:active {
  background-color: #bd9999;
}
.add-cart-btn:active {
  background-color: #bc8f8f;
  color: white;
}
.buy-btn {
  border: none;
  background-color: #bc8f8f;
  color: white;
  width: 100%;
  padding: 20px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  transition: 0.3s;
}
.add-cart-btn {
  border: 2px solid #bc8f8f;
  background-color: transparent;
  color: #bc8f8f;
  width: 100%;
  padding: 18.5px 20px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  transition: 0.3s;
}

.pro-about h2 {
  color: #bc8f8f;
  margin: 20px 0;
}

.pro-about p {
  text-align: justify;
}
/* Responsive Styles */
@media (max-width: 768px) {
  .thumbnail-container {
    display: none; /* Hide thumbnails on medium and small screens */
  }

  .main-image-container {
    max-width: 100%;
  }

  .product-page {
    flex-direction: column;
    align-items: center;
  }
  .right {
    margin-top: 20px;
  }
}
/* Large Screen Styles */
@media (min-width: 1024px) {
  .main-image-container {
    max-width: 600px; /* Increase the size of the main image for large screens */
  }
}

.addtocart-msg {
  z-index: 999;
  position: absolute;
  top: 120px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  padding: 40px;
  border: 3px solid #bc8f8f;
  border-radius: 18px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  gap: 30px;
}
.addtocart-msg h2 {
  font-size: 28px;
}

@media (max-width: 900px) {
  .addtocart-msg {
    padding: 30px;
  }
  .addtocart-msg h2 {
    font-size: 24px;
  }
  .addtocart-msg img {
    width: 34px;
  }
}
@media (max-width: 600px) {
  .addtocart-msg {
    padding: 20px;
  }
  .addtocart-msg h2 {
    font-size: 20px;
  }
  .addtocart-msg img {
    width: 28px;
  }
}
